// This is your project's main Less file.
// Project-specific Less rules go after the imports.

// Import Capital Framework.
@import (less) "cf-forms.less";
@import (less) "cf-pagination.less";
@import (less) "cf-grid.less";
@import (less) "cf-layout.less";
@import (less) "cf-icons.less";
@import (less) "cf-tables.less";
@import (less) "cf-typography.less";
@import (less) "cf-expandables.less";
@import (less) "cf-buttons.less";
@import (less) "cf-core.less";

// Import the brand color palette.
@import (less) "brand-palette.less";

// Uncomment to import Avenir font on CFPB projects.
// If this project is on a non-CFPB domain,
// use this file as a template for adding web fonts.
@import (less) 'licensed-fonts.css';

// Override the Capital Framework theme variables with colors from the brand color palette.
@import (less) "cf-theme-overrides.less";

// Import the Capital Framework enhancements
@import (less) "cf-enhancements.less";

@import (less) "charts.less";


// We've generated some sample rules for you. They're used with the index.html file.
// Please delete them before starting your project.

.intro-message {
    padding: 1em;
    background: @expandable__padded-bg;
    .webfont-medium();
}

.feature-header {
    display: inline-block;
    padding-bottom: unit(10px / @base-font-size-px, em);
    margin-bottom: unit(30px / @base-font-size-px, em);
    border-bottom: 4px solid @content_bar;

    &_name {
        display: inline-block;
        margin: 0;
        line-height: 1;
    }
    &_docs {
        display: inline-block;
        vertical-align: top;
        .webfont-regular();
    }
    &_name + &_docs {
        margin-left: 1em;
    }
}

.color-demo {
    .grid_column( 4 );
    height: 30px;
}

.color-demo__btn-bg {
    background-color: @btn-bg;
}

.color-demo__btn-secondary-bg {
    background-color: @btn__secondary-bg;
}

.color-demo__btn-disabled-bg {
    background-color: @btn__disabled-bg;
}

.sample-grid {
    .grid_nested-col-group();
}

.sample-grid_col:before {
    content: "";
    display: block;
    width: 100%;
    height: 1em;
    background: @btn-bg;
}

.sample-grid_col-1 {
    .grid_column( 1 );
}

.sample-grid_col-3 {
    .grid_column( 3 );
}

.sample-grid_col-4 {
    .grid_column( 4 );
}

.sample-grid_col-6 {
    .grid_column( 6 );
}

.typography-demo {
    &_item {
        .u-clearfix();
    }
    &_item-header {
        float: left;
        width: unit(200px / @base-font-size-px, em);
        margin-right: @grid_gutter-width;
        text-align: right;
    }
    .spec {
        .webfont-regular();
        color: #75787B;
        font-size: 0.875em;
    }
}
